import css from './WebPage4.module.css'
import {Link} from 'react-scroll'
import {motion} from 'framer-motion'
import { MyContext } from '../../LayoutDir/MyContextProvider/MyContextProvider';
import { useContext } from 'react';

const ok = ''
const lst = [{
    'title': ' ',
    'q':ok,
    'w':ok,
    'e':ok,
    'r':ok,
},
{
    'title': '  ',
    'q':ok,
    'w':ok,
    'e':ok,
    'r':ok,
},{
    'title': '   ',
    'q':ok,
    'w':ok,
    'e':ok,
    'r':ok,
},
{
    'title': 'SEO-',
    'q':ok,
    'w':ok,
    'e':ok,
    'r':ok,
},{
    'title': ' ',
    'q':ok,
    'w':ok,
    'e':ok,
    'r':ok,
},{
    'title': '  ',
    'q':ok,
    'w':ok,
    'e':ok,
    'r':ok,
},{
    'title': '  Google',
        'q':ok,
        'w':ok,
        'e':ok,
        'r':ok,
},{
    'title': ' ',
        'q':'4 .',
        'w':'4 .',
        'e':'4 .',
        'r':'4 .',
},]
const WebPage4 = () => {
    const { modal, setModal } = useContext(MyContext);
    return ( 
<>
<section className={css.container}>
    <motion.h2  initial={{y:100}} whileInView={{y:0}} className={css.header}>   </motion.h2> 
    <motion.table className={css.table}>
    <tr className={css.strong} style={{borderBottom:'1px solid #06466d'}} >
        <td className={css.column1}>   </td>
        <td className={css.column2}>Landing Page</td>
        <td className={css.column3}> </td>
        <td className={css.column4}> </td>
        <td className={css.column5}> </td>
    </tr>
    {   lst.map((item, index) => (
        index % 2 ? (
        <tr className={css.strong} style={{background:'#0b181f'}} >
            <td className={css.column} >{item.title}</td>
            <td className={css.column}>{item.q}</td>
            <td className={css.column}>{item.w}</td>
            <td className={css.column}>{item.e}</td>
            <td className={css.column}>{item.r}</td>
        </tr>
        ) : 
        <tr className={css.strong} >
            <td className={css.column}>{item.title}</td>
            <td className={css.column}>{item.q}</td>
            <td className={css.column}>{item.w}</td>
            <td className={css.column}>{item.e}</td>
            <td className={css.column}>{item.r}</td>
        </tr>
        ))}
        <tr className={css.strongButton} style={{borderBottom:'1px solid #06466d'}} >
        <td className={css.columnButton} style={{color:'rgb(68, 159, 187)',fontWeight:'bold',fontSize:'1.7vw'}}></td>
        <td className={css.columnButton}>
            <h3 className={css.columnPrice}> 15 000 .</h3>
            <a className={css.columnButtonOn} onClick={()=>{setModal(true)}}></a>
        </td>
        <td className={css.columnButton}>
            <h3 className={css.columnPrice}> 35 000 .</h3>
            <a className={css.columnButtonOn} onClick={()=>{setModal(true)}}></a>
        </td>
        <td className={css.columnButton}>
            <h3 className={css.columnPrice}> 80 000 .</h3>
            <a className={css.columnButtonOn} onClick={()=>{setModal(true)}}></a>
        </td>
        <td className={css.columnButton}>
            <h3 className={css.columnPrice}></h3>
            <a className={css.columnButtonOn} onClick={()=>{setModal(true)}}></a>
        </td>
    </tr>
    </motion.table>    
</section>
</>
     );
}
 
export default WebPage4;