import css from './WebPage3.module.css'
import reactImg from './img/react.png'
import gsap from "gsap"

import { useTransform, motion,useViewportScroll } from 'framer-motion'
import { useEffect } from 'react'


import speed from './img/speed.png'
import interfac from './img/interface.png'
import spa from './img/spa.png'
import seo from './img/seo.png'
import seo1 from './img/seo1.png'
import kasp from './img/kasp.png'
import scale from './img/scale.png'
import opt from './img/opt.png'
import eco from './img/eco.png'
import mobile from './img/mobile.png'
import skill from './img/skill.png'
import js from './img/js.png'

const lst = [{
    'title':'   ',
    'description':'React      ,          .',
    'img':speed,
    'alt':`    -`
},{
    'title':'-  JavaScript',
    'description':'React,   JavaScript,   -,        -.',
    'img':js,
    'alt':` -    `
},
{
    'title':'  ',
    'description':'    React,        ,    .',
    'img':interfac,
    'alt':` -   `
},
{
    'title':'  (SPA)',
    'description':'React   SPA.  ,       ,       .',
    'img':spa,
    'alt':`     -`
},
{
    'title':'SEO-',
    'description':'React          Next.js   SEO,           .',
    'img':seo1,
    'alt':`UX  -    `
},
{
    'title':'   ',
    'description':'C  React       -  ,         .',
    'img':seo,
    'alt':`SEO  -   `
},
{
    'title':' ',
    'description':'React    ,     (, XSS),       .',
    'img':kasp,
    'alt':`JavaScript     `
},
{
    'title':'  ',
    'description':'  React       ,      .',
    'img':scale,
    'alt':`HTML  CSS  -   `
},
{
    'title':' ',
    'description':' React,    DOM    (lazy loading),   ,       .',
    'img':opt,
    'alt':`      `
},
{
    'title':'   ',
    'description':'React    ,    ,        .',
    'img':eco,
    'alt':`  Figma  -`
},
{
    'title':'   ',
    'description':' React     ,           .',
    'img':mobile,
    'alt':`    Figma`
},
{
    'title':'  ',
    'description':'  React                  .',
    'img':skill,
    'alt':`HTML  CSS   -`
},]

const WebPage3 = () => {

    const { scrollY } = useViewportScroll();

    useEffect(() => {
        gsap.to('.fixed-img', {position: 'fixed', top: '20%', left: '0%', transform: 'translate(-50%, -50%)' ,duration:0});
        function handleScroll() {
            const currentY = scrollY.get();
            if (currentY > 4500 && currentY < 8300) {
                //     
                document.body.style.overflow = 'hidden';
                gsap.to('.fixed-img',{opacity:.2})

            } else {
                //   
                document.body.style.overflow = '';
                gsap.to('.fixed-img',{opacity:0})
            }
        }

        const unsubscribeY = scrollY.onChange(handleScroll);

        return () => {
            unsubscribeY();
            document.body.style.overflow = '';
        };
    }, [scrollY]);


    return ( 
<>
<section className={css.container}>
            


        <div className={css.textContainer}>
            <motion.h2 className={css.header}> <span>React.js</span></motion.h2>
            <div  className={css.cardContainer}>
                {lst.map((item)=>(
                    <motion.div  initial={{ opacity:0 }} viewport={{amount:.7}}  whileInView={{ opacity:1 }} transition={{duration:1}} className={css.card}>
                        <div className={css.cardImgConrtainer}>
                            <img className={css.cardImg} src={item.img} alt={item.alt} />
                        </div>
                        <div className={css.cardContent}>
                            <h3 className={css.cardHeader}>{item.title}</h3>
                            <p className={css.cardFullText}>{item.description}</p>
                        </div>
                    </motion.div>
                ))}
                {/* <motion.h2 className={css.header}>{height}</motion.h2> */}
            </div>
        </div>
        <motion.img className={`${css.img} fixed-img`} src={reactImg} alt={lst[0].alt} />
</section>
</>
     );
}
 
export default WebPage3;