import { MyContext } from '../MyContextProvider';
import css from './CalcPage13Mobile.module.css'
import { useEffect, useRef } from 'react';
import { useContext } from 'react';
import {motion} from 'framer-motion'
import gsap from 'gsap';
import next1 from '../img/next1.png'
import back1 from '../img/back1.png'
import InputMask from 'react-input-mask';
import axios from 'axios';
import numberValidator from "../../../utils/numberValidator";


const token = '6979178148:AAHqJu4xrlhBTKhnOuysouWtDJsO164UaN0'
const chatId = '-4014090823'
const URI_API = `https://api.telegram.org/bot${ token }/sendMessage`


const CalcPage13Mobile = () => {
    
    const contRef = useRef()
    const { tz,priceMax, setPriceMax,priceMin,
        setPriceMin,workMin,setWorkMin,setDisignUniq,
        workMax,setWorkMax,setLending,setHostingPtice,setServerPrice,
        setCorp,setShop,setPage1,countPage, setCountPage,semantic,setSemantic,
        semanticCount,setSemanticCount,setPage11,disign0, setDisign0,disign1, setDisign1,
        disign2, setDisign2,disignApple, setDisignApple,disignUniq, setDisignsUniq,
        logoDisign, setlogoDisign,firmStyle,setFirmStyle,setPage12,
        page13,setPage13,staticDev,hostingPrice,serverPrice,
        adaptiveDev,setadAptiveDev,adinteractiveDev,setadInteractiveDev,form,setForm ,
        bot,setBot,calc,setCalc,pay,setPay,setStaticDev,inputNumber,setInputNumber,windRef,formValid, setFormValid,
        formValidRef,formValidFonRef,number, setNumber} = useContext(MyContext); 

        useEffect(()=>{
            setNumber('');
            setInputNumber('1px solid #cae2e3')
        },[page13])
        let message = `<b>  !</b>\n`
            message += `<b>: ${number}</b>\n`
            message += `<b></b>\n`
            if(tz){message += `     ${tz}.\n`}
            message += `      ${countPage}.\n`
            if(countPage>1){message += `     ,  . ${serverPrice} .\n`}else{
                message += `    ,    ${hostingPrice} .\n`
            }
            if(semantic){message += `     ${semantic*semanticCount}.\n`}
            if(semantic){message += `     ${countPage}.\n`}
            if(disign0){message += `     \n`}
            if(disign1){message += `    \n`}
            if(disign2){message += `      ${countPage>1?disign2*countPage*.7:disign2}.\n`}
            if(disignApple){message += `       Apple ${disignApple}.\n`}
            if(disignUniq){message += `      ${disignUniq}.\n`}
            if(logoDisign){message += `    (3 ) ${logoDisign}.\n`}
            if(firmStyle){message += `     ${firmStyle}.\n`}
            if(staticDev){message += `    ${countPage>1?staticDev*countPage*.7:staticDev}.\n`}
            if(adaptiveDev){message += `    ${countPage>1?adaptiveDev*countPage*.7:adaptiveDev}.\n`}
            if(adinteractiveDev){message += `  ,  . ${countPage>1?adinteractiveDev*countPage:adinteractiveDev}.\n`}
            if(form){message += `      ${form}.\n`}
            if(bot){message += `    Telegram      ${bot}.\n`}
            if(calc){message += `    ${calc}.\n`}
            if(pay){message += `        ${pay}.\n`}
            message += `:   ${priceMin} .  ${priceMax} .`

            const handleEstimateClick = (event) => {
                if(numberValidator(number)){//  
                axios.post(URI_API, {
                    chat_id:chatId,
                    parse_mode:'html',
                    text: message
                })
                setFormValid('flex')
                
                gsap.from(formValidRef.current,{opacity:0,duration:4})
                gsap.to(formValidRef.current,{opacity:0,duration:3,delay:1})
                gsap.to(formValidFonRef.current,{opacity:0,duration:3,delay:1})
                gsap.to(formValidFonRef.current,{opacity:1,delay:15})
                gsap.to(formValidRef.current,{opacity:1,delay:15})

                setNumber('')
                setPriceMax(0)
                setPriceMin(0)
                setWorkMin(0)
                setWorkMax(0)
                setLending(0)
                setCorp(0)
                setShop(0)
                setPage1(true)
                setPage11(0)
                setPage13(0)
                setCountPage(1)
                setSemantic(0)
                setSemanticCount(1)
                setDisign0(0)
                setDisign1(0)
                setDisign2(0)
                setDisignApple(0)
                setDisignUniq(0)
                setlogoDisign(0)
                setFirmStyle(0)
                setStaticDev(0)
                setadAptiveDev(0)
                setadInteractiveDev(0)
                setForm(0)
                setBot(0)
                setCalc(0)
                setPay(0)
                setNumber('')
                setHostingPtice(0)
                setServerPrice(0)
                setInputNumber('1px solid #cae2e3')
        
            }else{
                setInputNumber('1px solid red')
            }
            }

            const getBack = () =>{
                setPage13(false)
                setPage12(true)
                gsap.to(windRef.current,{opacity:0,duration:.1})
            }

    return (
<>
{page13&&
    <section className={css.container}>
        <motion.div ref={contRef} initial={{opacity:0}} whileInView={{opacity:1}} transition={{duration:.4}} className={css.wind1}>
        <div className={css.nestedContainer1}>
                <h2 className={css.windHeader}></h2>
                {countPage>1?<div className={css.tz}>
                    <span className={css.tzText}>     ,  .</span> 
                    <div className={css.cardPrice}>{serverPrice}.</div>     
                </div>:<div className={css.tz}>
                    <span className={css.tzText}>    ,   </span>  
                    <div className={css.cardPrice}>{hostingPrice}.</div>    
                </div>}
                {tz?<div className={css.tz}>
                    <span className={css.tzText}>    </span>
                    <div className={css.cardPrice}>{tz}.</div>  
                       
                </div>:<></>}
                <div className={css.tz}>
                    <span className={css.tzText}>     </span> 
                    <div className={css.cardPrice}>{countPage}.</div>       
                </div>
                {semantic?<div className={css.tz}>
                    <span className={css.tzText}>    </span>  
                    <div className={css.cardPrice}>{semantic*semanticCount}.</div>    
                </div>:<></>}
                {semantic?<div className={css.tz}>
                    <span className={css.tzText}>    </span>     
                    <div className={css.cardPrice}>{countPage}.</div> 
                </div>:<></>}
                {disign0?<div className={css.tz}>
                    <span className={css.tzText}>    </span>    
                    <div className={css.cardPrice}>{semantic*semanticCount}.</div>    
                </div>:<></>}
                {disign1?<div className={css.tz}>
                    <span className={css.tzText}>   </span>      
                    <div className={css.cardPrice}>{semantic*semanticCount}.</div>  
                </div>:<></>}
                {disign2?<div className={css.tz}>
                    <span className={css.tzText}>     </span> 
                    <div className={css.cardPrice}>{countPage>1?disign2*countPage*.7:disign2}.</div>       
                </div>:<></>}
                {disignApple?<div className={css.tz}>
                    <span className={css.tzText}>       Apple</span> 
                    <div className={css.cardPrice}>{disignApple}.</div>       
                </div>:<></>}
                {disignUniq?<div className={css.tz}>
                    <span className={css.tzText}>     </span>  
                    <div className={css.cardPrice}>{disignUniq}.</div>      
                </div>:<></>}
                {logoDisign?<div className={css.tz}>
                    <span className={css.tzText}>    (3 )</span>     
                    <div className={css.cardPrice}>{logoDisign}.</div>   
                </div>:<></>}
                {firmStyle?<div className={css.tz}>
                    <span className={css.tzText}>    </span>      
                    <div className={css.cardPrice}>{firmStyle}.</div>  
                </div>:<></>}
                {staticDev?<div className={css.tz}>
                    <span className={css.tzText}>   </span>  
                    <div className={css.cardPrice}>{countPage>1?staticDev*countPage*.7:staticDev}.</div>      
                </div>:<></>}
                {adaptiveDev?<div className={css.tz}>
                    <span className={css.tzText}>   </span>   
                    <div className={css.cardPrice}>{countPage>1?adaptiveDev*countPage*.7:adaptiveDev}.</div>     
                </div>:<></>}
                {adinteractiveDev?<div className={css.tz}>
                    <span className={css.tzText}>  ,  .</span>  
                    <div className={css.cardPrice}>{countPage>1?adinteractiveDev*countPage:adinteractiveDev}.</div>      
                </div>:<></>}
                {form?<div className={css.tz}>
                    <span className={css.tzText}>     </span>     
                    <div className={css.cardPrice}>{form}.</div>   
                </div>:<></>}
                {bot?<div className={css.tz}>
                    <span className={css.tzText}>    Telegram     </span>   
                    <div className={css.cardPrice}>{bot}.</div>   
                </div>:<></>}
                {calc?<div className={css.tz}>
                    <span className={css.tzText}>   </span>  
                    <div className={css.cardPrice}>{calc}.</div>    
                </div>:<></>}
                {pay?<div className={css.tz}>
                    <span className={css.tzText}>       </span>   
                    <div className={css.cardPrice}>{pay}.</div>   
                </div>:<></>}
                <span className={`${css.tzText} ${css.warn}`}> ,            .</span>
                <div className={css.buttonContainer1}>
                <InputMask
                                       style={{border:inputNumber}}
                                           value={number}
                                           mask="+7(999)999-99-99"
                                           placeholder=' '
                                           className={css.InputPhone}
                                           onChange={(e)=>{setNumber(e.target.value)}}
                                       />
                    <div className={css.button1} onClick={handleEstimateClick}> </div></div>
                        
            </div>
            <div className={css.nestedContainer2}>
                
                <div className={css.priceContainer}>
                    <div className={css.timePrice}>
                        <h3 className={css.winsPriceHeader}></h3>
                        <span className={css.priceCount}> {workMin} .</span>
                        <span className={css.priceCount}> {workMax} .</span>
                    </div>
                    <div className={css.hr}></div>
                    <div className={css.timePrice}>
                        <h3 className={css.winsPriceHeader}></h3>
                        <span className={css.priceCount}> {priceMin} .</span>
                        <span className={css.priceCount}> {priceMax} .</span>
                    </div>
                </div>
                <div className={css.buttonContainer}>
                        <div className={css.button} onClick={()=>{getBack()}}>
                            <img className={css.mobileNextImg} src={back1} alt="" />
                        </div>
                    </div>
                <div className={css.consoleContainer}>
                    <div className={css.progressBarContainer}>
                        <motion.div initial={{width:'90%'}} whileInView={{width:'100%'}} transition={{duration:1}} className={css.progress}></motion.div>
                    </div>

                </div>

            </div>
            
        </motion.div>
</section>
}

</>
    );
}
 
export default CalcPage13Mobile;


