import css from './LandingPageMobile5.module.css'
import {motion} from 'framer-motion'

const lst = [{
    'number':'1',
    'header': '',
    'content':',  ,     ,  .'
},{
    'number':'2',
    'header': ' ',
    'content':'     ,          .'
},{
    'number':'3',
    'header': ' ',
    'content':'      ,   ,   .'
},
{
    'number':'4',
    'header': '  ',
    'content':'    ,       .'
},
{
    'number':'5',
    'header': ' ',
    'content':'    ,    .'
},
{
    'number':'6',
    'header': '',
    'content':'   ,      .'
},
{
    'number':'7',
    'header': ' ',
    'content':'     ,        .'
},
{
    'number':'8',
    'header': ' ',
    'content':'   ,     .'
},]

const LandingPageMobile5 = () => {
    const width = window.screen.width
    if (width>768){
        return ( 
            <>
            <section className={css.container}>
                <motion.h2 initial={{opacity:0}} whileInView={{opacity:.9}} transition={{duration:1}} className={css.header}>8     </motion.h2>
                <div className={css.cardContainer}>
                    {lst.map((item, index) => (
            
                    index % 2 ? (
                    <motion.div initial={{ x: -300 }} viewport={{amount:.7}}  whileInView={{ x: 0 }} className={css.card} style={{marginRight:'24vw'}}>
                        <span className={css.cardNumber}>{item.number}</span>
                        <div className={css.cardContent}>
                            <h3 className={css.cardHeader}>{item.header}</h3>
                            <p className={css.cardFullText}>{item.content}</p>
                        </div>
                    </motion.div>
                    ) : 
                    <motion.div initial={{ x: 300 }} viewport={{amount:0.7}}  whileInView={{ x: 0 }} className={css.card} style={{marginLeft:'24vw'}}>
                        <span className={css.cardNumber}>{item.number}</span>
                        <div className={css.cardContent}>
                            <h3 className={css.cardHeader}>{item.header}</h3>
                            <p className={css.cardFullText}>{item.content}</p>
                        </div>
                    </motion.div>
                    ))}
                </div>
            </section>
            </>
                 );
    }else{
        return ( 
            <>
            <section className={css.container}>
            <motion.h2 initial={{opacity:0}} whileInView={{opacity:.8}} transition={{duration:1}} className={css.header}>8     </motion.h2>
                <div className={css.cardContainer}>
                {lst.map((item, index) => (
            
            index % 2 ? (
            <motion.div initial={{ x: -300 }}  whileInView={{ x: 0 }} className={css.card} >
                <span className={css.cardNumber}>{item.number}</span>
                <div className={css.cardContent}>
                    <h3 className={css.cardHeader}>{item.header}</h3>
                    <p className={css.cardFullText}>{item.content}</p>
                </div>
            </motion.div>
            ) : 
            <motion.div initial={{ x: 300 }}  whileInView={{ x: 0 }} className={css.card} >
                <span className={css.cardNumber}>{item.number}</span>
                <div className={css.cardContent}>
                    <h3 className={css.cardHeader}>{item.header}</h3>
                    <p className={css.cardFullText}>{item.content}</p>
                </div>
            </motion.div>
            ))}
                </div>
            </section>
            </>
                 );     
    }

}
 
export default LandingPageMobile5;