import css from './WebPage3.module.css'
import {motion} from 'framer-motion'

const lst = [
    {
    'number': '1',
    'header': '    ',
    'content': `       ,
           ,    
       ,      
            -.`
    },
    {
    'number': '2',
    'header': ' ',
    'content': `    ,   
             
      .       
          .`
    },
    {
    'number': '3',
    'header': '  ',
    'content': `       
        .    
         ,     ,
            .`
    },
    {
    'number': '4',
    'header': '  ',
    'content': `         ,
           .    ,
       -,     SEO-.`
    },
    ]

const WebPage31 = () => {

   
    return ( 
        <>
        <section className={css.container}>
            <h2 className={css.header}>     ?</h2>
            <div className={css.cardContainer}>
            {lst.map((item, index) => (    
                index %2?   
                <motion.div initial={{ x:200 }}   whileInView={{ x:0 }} className={css.card}>
                <span className={css.cardNumber}>{item.number}</span>
                <div className={css.cardContent}>
                    <h3 className={css.cardHeader}>{item.header}</h3>
                    <p className={css.cardFullText}>{item.content}</p>
                </div>
                </motion.div>
                :
                <motion.div initial={{ x:-200 }}   whileInView={{ x:0 }} className={css.card}>
                <span className={css.cardNumber}>{item.number}</span>
                <div className={css.cardContent}>
                    <h3 className={css.cardHeader}>{item.header}</h3>
                    <p className={css.cardFullText}>{item.content}</p>
                </div>
                </motion.div>
                    ))}
            </div>
        </section>
        </>
            );

}
 
export default WebPage31;