
import { useLayoutEffect } from 'react';
import css from './Login.module.css'

const Login = () => {
    useLayoutEffect(()=>{
        document.title = "";
    })
    return ( 
<>
<section className={css.container}>
    <h1 className={css.header}></h1>
</section>
</>
     );
}
 
export {Login};