import css from './WebPage2.module.css'
import { Link } from 'react-router-dom';
import  { motion} from "framer-motion";

import bisnes from './img/bisnes.png'
import corp from './img/corp.png'
import landing from './img/landing.png'
import shop from './img/shop.png'

const lst = [{
    'title':'LandingPage',
    'description':`
                  .
                 .
                  Landing Page    .`,
        'link':'/landing',
        'img': landing,
        'alt': `  -   `

},{
    'title':' ',
    'description':`
     -     ,
           .
          -,     
        .`,
        'link':'/corp',
        'img': corp,
        'alt': `      CSS grid  flexbox`

},{
    'title':' ',
    'description':`
        -    .
            -,
            .    
            .`,
        'link':'/shop',
        'img': shop,
        'alt': `-    `

},{
    'title':' ',
    'description':`
            -.
            ,
           .
              3-6  .`,
        'link':'/card',
        'img': bisnes,
        'alt': ` -     `

},]

const WebPage2 = () => {
    
    return ( 
<section className={css.container}>
    
    <motion.h2 initial={{opacity:0}} whileInView={{opacity:1}} transition={{duration:1}} className={css.header}>   ?</motion.h2>
    <div className={css.cardContainer}>
        {lst.map((item,index)=>(
            <motion.div initial={{opacity:0}} whileInView={{opacity:1}}  className={css.card}>
            <div className={css.imgContainer}>
                <h3 className={css.cardHeader}>{item.title}</h3>
                <motion.img initial={{y:100}} whileInView={{y:0}} className={css.cardImg} src={item.img} alt={item.alt} />
                </div>
                <div className={css.contentContainer}>
                <p className={css.fullText}>{item.description}</p>
                    <motion.div initial={{y:100}} whileInView={{y:0}} className={css.buttonContainer}>
                        <Link className={css.cardButtonLink}  to={item.link}></Link>
                        <Link className={css.cardButtonLink}  to='/calc'></Link>
                    </motion.div>
                </div>
            </motion.div> 
        ))}
    </div>
</section>
     );
}
 
export default WebPage2;