import css from './CorpPage3.module.css'
import  { motion} from "framer-motion";

import landing from './img/landing1.png'

const CorpPage3 = () => {
    return ( 
<>
<section className={css.container}>
 
    <motion.div initial={{x:-100}} whileInView={{x:0}} viewport={{amount:.7}} className={css.contentContainer}>
        <motion.div initial={{opacity:0}} whileInView={{opacity:.8}} transition={{duration:1}} viewport={{amount:.7}} className={css.cardHeader}>   </motion.div>
        <motion.div initial={{opacity:0}} whileInView={{opacity:.8}} transition={{duration:1}} viewport={{amount:.7}} className={css.cardFullText}>
                 , 
                   . 
           ,      
         .          
          .
        </motion.div>
    </motion.div>
    <motion.img initial={{x:100,scale:0.7}} whileInView={{x:0,scale:1}} viewport={{amount:.7}} className={css.img} src={landing} alt="   " />
</section>
</>
     );
}
 
export default CorpPage3;