import css from './CardPage2.module.css'
import  { motion} from "framer-motion";
import reactive from './img/react.png'
import cardimg1 from './img/cardImg1.png'
import cardimg2 from './img/cardImg2.png'
import cardimg3 from './img/cardImg3.png'


const lst = [
    {
      'header': ' ',
      'description': '  ,     ,     -  .'
    },
    {
      'header': '   ',
      'description': '             .'
    },
    {
      'header': ' ',
      'description': '   ,           .'
    },
    {
      'header': ' ',
      'description': '   ,       .'
    },
    {
      'header': ' ',
      'description': '  ,          .'
    },
    {
      'header': '  ',
      'description': '    -     ,       .'
    }
  ]


const CardPage2 = () => {
    return (  
<section className={css.container}>
    <img className={css.fon} src={reactive} alt="" />
    <div className={css.nestedContainer}>
        <motion.h2 initial={{opacity:0}} whileInView={{opacity:.8}} transition={{duration:1}} viewport={{amount:.7}} className={css.header}>{lst[0].header}</motion.h2>
        <motion.p initial={{opacity:0}} whileInView={{opacity:.8}} transition={{duration:1}} viewport={{amount:.7}} className={css.fullText}>{lst[0].description}</motion.p>
    </div>
    <motion.div initial={{x:100}} whileInView={{x:0}} viewport={{amount:.7}} className={css.nestedContainer}>
        <div className={css.card}>
            <div className={css.imgContainers}>
                <img className={css.img} src={cardimg1} alt="" />
            </div>        
            <div className={css.contentContainer}>
            <div className={css.cardHeader}>{lst[1].header}</div>
            <div className={css.cardFullText}>{lst[1].description}</div>
            </div>
        </div>
        <div className={css.card}>
        <div className={css.imgContainers}>
                <img className={css.img} src={cardimg2}  alt="" />
            </div>        
            <div className={css.contentContainer}>
                <div className={css.cardHeader}>{lst[2].header}</div>
                <div className={css.cardFullText}>{lst[2].description}</div>
            </div>
        </div>
        <div className={css.card}>
        <div className={css.imgContainers}>
                <img className={css.img} src={cardimg3}  alt="" />
            </div>        
            <div className={css.contentContainer}>
                <div className={css.cardHeader}>{lst[3].header}</div>
                <div className={css.cardFullText}>{lst[3].description}</div>
            </div>
        </div>
    </motion.div>
</section>
    );
}
 
export default CardPage2;